import { NextRequest, NextResponse } from "next/server";
import clientPromise from "@/lib/dbConnect";
import Customer from "@/models/Customer";
import { verifyToken } from "@/lib/auth";
import mongoose from "mongoose";

// Helper function to verify authentication
async function verifyAuth(request: NextRequest) {
    const token = request.cookies.get("token")?.value;
    if (!token) return null;

    const decoded = verifyToken(token);
    if (!decoded) return null;

    return decoded;
}

// GET - L·∫•y chi ti·∫øt kh√°ch h√†ng
export async function GET(
    request: NextRequest,
    { params }: { params: Promise<{ id: string }> },
) {
    try {
        const auth = await verifyAuth(request);
        if (!auth) {
            return NextResponse.json(
                { success: false, message: "Kh√¥ng ƒë∆∞·ª£c ph√©p truy c·∫≠p" },
                { status: 401 },
            );
        }

        await clientPromise;
        await mongoose.connection.db;

        // Await params trong Next.js 13+ App Router
        const { id } = await params;

        // Debug logging
        console.log("GET - Received ID:", id);
        console.log("GET - ID type:", typeof id);
        console.log("GET - Params:", await params);

        if (!mongoose.Types.ObjectId.isValid(id)) {
            console.log("GET - ID validation failed for:", id);
            return NextResponse.json(
                { success: false, message: "ID kh√°ch h√†ng kh√¥ng h·ª£p l·ªá" },
                { status: 400 },
            );
        }

        const customer = await Customer.findById(id);

        if (!customer) {
            return NextResponse.json(
                { success: false, message: "Kh√¥ng t√¨m th·∫•y kh√°ch h√†ng" },
                { status: 404 },
            );
        }

        return NextResponse.json({
            success: true,
            data: customer,
        });
    } catch (error) {
        console.error("GET customer error:", error);
        return NextResponse.json(
            { success: false, message: "C√≥ l·ªói x·∫£y ra. Vui l√≤ng th·ª≠ l·∫°i." },
            { status: 500 },
        );
    }
}

// PUT - C·∫≠p nh·∫≠t kh√°ch h√†ng
export async function PUT(
    request: NextRequest,
    { params }: { params: Promise<{ id: string }> },
) {
    try {
        const auth = await verifyAuth(request);
        if (!auth) {
            return NextResponse.json(
                { success: false, message: "Kh√¥ng ƒë∆∞·ª£c ph√©p truy c·∫≠p" },
                { status: 401 },
            );
        }

        await clientPromise;
        await mongoose.connection.db;

        // Await params trong Next.js 13+ App Router
        const { id } = await params;
        const body = await request.json();

        if (!mongoose.Types.ObjectId.isValid(id)) {
            return NextResponse.json(
                { success: false, message: "ID kh√°ch h√†ng kh√¥ng h·ª£p l·ªá" },
                { status: 400 },
            );
        }

        const customer = await Customer.findByIdAndUpdate(
            id,
            { ...body, updatedAt: new Date() },
            { new: true, runValidators: true },
        );

        if (!customer) {
            return NextResponse.json(
                { success: false, message: "Kh√¥ng t√¨m th·∫•y kh√°ch h√†ng" },
                { status: 404 },
            );
        }

        return NextResponse.json({
            success: true,
            message: "C·∫≠p nh·∫≠t kh√°ch h√†ng th√†nh c√¥ng",
            data: customer,
        });
    } catch (error) {
        console.error("PUT customer error:", error);
        return NextResponse.json(
            { success: false, message: "C√≥ l·ªói x·∫£y ra. Vui l√≤ng th·ª≠ l·∫°i." },
            { status: 500 },
        );
    }
}

